import React, {Component} from 'react';
import Layout from '../components/Layout'
import {
  Button,
  Divider,
  Dropdown,
  Grid,
  Header,
  Icon,
  Image,
  Label,
  Menu,
  Message,
  Segment,
  Table,
  Input
} from 'semantic-ui-react'

const TransactionSearch = props => (
  <Grid.Row>
    <Grid.Column>
      <Message>
        <Header as='h1'>Search Balance</Header>
        <p>
          This is a template for a simple marketing or informational website. It includes a large
          callout called a jumbotron and three supporting pieces of content. Use it as a starting
          point to create something more unique.
        </p>
        <div>
          <Input placeholder='Address...' icon='search' value={props.address} onChange={e => props.addressUpdate(e.target.value)}/>
          <Button color='blue' onClick={props.onSearch}>Search</Button>
        </div>
      </Message>
    </Grid.Column>
  </Grid.Row>
)

export class Main extends Component {
  state = {
    search: null,
    results: null,
    address: ''
  }
  searchTransactions = () => {
    const {address} = this.state
    console.log('graphql queries', address)
  }

  addressUpdate = (address) => {
    this.setState({address})
  }

  searchTransactionsScreen = () => {
    this.setState({ search: 'transactions'})
  }

  render(){
    return (
      <Layout>
        <Grid container style={{ padding: '10em 0em' }}>
          <Grid.Row>
            <Grid.Column>
              <Header as='h1' dividing>
                Balanc3 Take Home App
              </Header>
            </Grid.Column>
          </Grid.Row>
          <Grid.Row>
            <Grid.Column>
              <Message>
                <Header as='h1'>Search By Address</Header>
                <p>
                  Choose whether you want to look up transactions or balance of an address!
                </p>
                <Button color='blue' onClick={this.searchTransactionsScreen}>Transactions</Button>
                <Button color='blue'>Balance</Button>
              </Message>
            </Grid.Column>
          </Grid.Row>
          {this.state.search === 'transactions' && (
            <TransactionSearch
              onSearch={this.searchTransactions}
              addressUpdate={this.addressUpdate}
              address={this.state.address}
            />)
          }
          {this.state.search === 'balance' && (
            <TransactionSearch
              onSearch={this.searchTransactions}
              addressUpdate={this.addressUpdate}
              address={this.state.address}
            />)
          }
          {this.state.results}
        </Grid>
      </Layout>
    );
  }
}

export default Main



